import React,{useState,useEffect} from 'react';
import $ from 'jquery';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

function ClassTable(props){
  let classes = useFFClass(props);
  let styles = useStyles();

  let handleClick = (c) => {
    props.handleSelection(c);
  };

  let i=0;
  let prevDate=null;

  return (
    <Paper elevation={3} className={styles.tableWrapper}>
      <TableContainer className={styles.tableContainer} style={{maxHeight: classes.length?'':36}}>
        <Table stickyHeader className={styles.table} size="small">
          <TableHead className={styles.thead}>
            <TableRow>
              <TableCell>ID</TableCell>
              <TableCell>Club</TableCell>
              <TableCell>Class</TableCell>
              <TableCell>Date</TableCell>
              <TableCell>Time</TableCell>
              <TableCell>Capacity</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {classes.sort((a,b)=>a.timestamp-b.timestamp).map(c => {
              let currPrevDate = prevDate;
              prevDate = c.date;
              return <TableRow key={c.classId}
                onClick={() => handleClick(c)}
                style={{
                  backgroundColor:props.selected?.classId===c.classId?'#00d8f57d':'',
                  boxShadow:currPrevDate==null||c.date==currPrevDate?'':'#b6ccdd 0 2px 0 0 inset'
                }} hover>
                  <TableCell>{c.classId}</TableCell>
                  <TableCell>{c.club}</TableCell>
                  <TableCell>{c.class}</TableCell>
                  <TableCell>{c.date} ({c.day})</TableCell>
                  <TableCell>{c.start} - {c.end}</TableCell>
                  <TableCell>{c.capacity}</TableCell>
                </TableRow>;
            })}
          </TableBody>
        </Table>
      </TableContainer>
    </Paper>
  );
}

const useStyles = makeStyles({
  tableWrapper: {
    maxWidth: 1000,
    margin: '5px 0',
    backgroundColor: '#ffffffe0'
  },
  tableContainer: {
    maxHeight: 'calc(100vh - 20px)',
    transition: 'max-height 0.5s'
  },
  table: {
    minWidth: 650,
    userSelect: 'none'
  },
  thead:{
    '& th': {
      fontWeight: 'bold',
      backgroundColor: '#b6ccdd'
    }
  }
});

export default ClassTable;

let reqId = 0;

function useFFClass(props){
  let [classes, setClasses] = useState([]);

  useEffect(function(){
    async function searchClass(page=1){
      var data = {
        programIds: props.paramClasses,
        fromDate: parseInt(Date.now()/1000 + props.fromDateAdj),
        toDate: parseInt(Date.now()/1000 + props.toDateAdj),
        pageSize: 100,
        pageNumber: page,
        distance: 2147483647,
        latitude: 0,
        longitude: 0
      };
      if(props?.paramClub !== 'FFP'){
        data.clubId = props.paramClub;
      }
      console.log("Search Start");
      console.log(data);
      var res = await $.ajax({
        type: "GET",
        url: "https://api-mobile.circuithq.com/api/v2/class/search",
        dataType: 'json',
        headers: {
          "Authorization": props.accessToken,
          "User-Country-Code": "sg"
        },
        data: data
      });
      function toTime(timestamp){
        var d = new Date(timestamp*1000);
        return d.toTimeString().substring(0,5);
      }
      function toDate(timestamp){
          var d = new Date(timestamp*1000);
          return d.toLocaleDateString();
      }
      function toDay(timestamp){
          var d = new Date(timestamp*1000);
          return ['Sun','Mon','Tue','Wed','Thu','Fri','Sat'][d.getDay()];
      }
      var classes = res.data;
      classes = classes.map( c => {
          return {
              classId:c.classId,
              class:c.name,
              club:c.club.name,
              clubId:c.club.clubId,
              date:toDate(c.timeStart),
              day:toDay(c.timeStart),
              timestamp:c.timeStart,
              duration:c.timeEnd-c.timeStart,
              start:toTime(c.timeStart),
              end:toTime(c.timeEnd),
              capacity:c.capacity,
              address:c.club.address
          }
      }).filter( c => c.capacity > 0 || !props.accessToken );
      if(props.paramClub === 'FFP') classes = classes.filter(c=>c.club.indexOf('FFP')===0);
      if(props.paramTime === 'No Work') classes = classes.filter(c=>c.day==='Sat'||c.day==='Sun'||parseInt(c.end)<=11||parseInt(c.start)>=20);
      return classes;
    }
    if(props.paramClasses){
      (async (currReqId) => {
        setClasses([]);
        let classes = await searchClass();
        if(currReqId === reqId){
          setClasses(classes);
          if(classes.length < 30){
            let classes2 = await searchClass(2);
            if(currReqId === reqId){
              setClasses([...classes, ...classes2]);
            }
          }
        }
      })(++reqId);
    }
  }, [props.fromDateAdj, props.toDateAdj, props.paramClub, props.paramClasses, props.paramTime, props.accessToken]);

  return classes;
}